$theme_color: #333fff; // 主题色
$gray_color: #646464; // 灰色值
$warning_color: #ffb140; // 警告提示色
$danger_color: #ff4f4d; // 危险色
$header_height: 60px; // 顶部导航栏的高度
$side_nav_width: 260px; // 侧边栏的宽度
.ant-tabs {
    // tabs标签页切换
    .ant-tabs-tab {
        font-weight: 900;
    }
}

@mixin form_item($display, $label_width) {
    // 表单控件中label和表单的排列样式
    .ant-form-item-label {
        display: $display;
        min-width: $label_width;
        text-align: left;
        label {
            color: $gray_color;
        }
    }
    .ant-form-item-control-wrapper {
        display: $display;
    }
}

@mixin ant_card {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        border: none;
    }
}

@mixin left_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        cursor: $cursor;
        .ant-card-head {
            border: none;
            position: relative;
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                width: 6px;
                height: 30px;
                background-color: $theme_color;
                border-radius: 3px;
            }
        }
    }
}

@mixin top_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        text-align: center;
        padding: 25px 0;
        cursor: $cursor;
        border: 1px solid rgba(0, 0, 0, 0);
        transition: border-color 0.3s;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            left: 50%;
            transform: translateX(-50%);
            top: -1px;
            width: 30px;
            height: 6px;
            background: $theme_color;
            border-radius: 3px;
        }
        &:hover {
            border: 1px solid $theme_color;
        }
        .ant-card-head {
            border: none;
            min-height: 0;
            .ant-card-head-title {
                padding: 0;
            }
        }
        .ant-card-body {
            padding: 0;
        }
    }
}
.home {
    padding: 20px;
    .cluster {
        border-bottom: 1px solid #dadada;
        padding-bottom: 20px;
        .title {
            line-height: 60px;
        }
        .category {
            .category_item {
                display: inline-block;
                margin-right: 50px;
                .trouble_btn {
                    background-color: $warning_color;
                    border: none;
                }
                .category_name {
                    display: inline-block;
                    margin-left: 10px;
                    font-size: 18px;
                    font-weight: 600;
                    color: #222;
                }
            }
        }
    }
    .cluster_wrap {
        padding: 30px 0;
        .cluster_list {
            .ant-card {
                box-shadow: 3px 3px 12px 0px #DDDCDC;
                .ant-card-head {
                    background-color: $theme_color;
                    color: #fff;
                    border-radius: 20px 20px 0px 0px;
                    .ant-card-head-wrapper {
                        .ant-card-head-title {
                            flex: none;
                            font-size: 18px;
                        }
                        .ant-card-extra {
                            color: #fff;
                            font-size: 12px;
                            float: left;
                            margin-left: 10px;
                            padding: 2px 15px;
                            background: #6d76fe;
                            border-radius: 3px;
                            border: 1px solid #c6c9fa;
                        }
                    }
                }
            }
            .unhealth_cluster {
                .ant-card-head {
                    background-color: $warning_color;
                    .ant-card-head-wrapper {
                        .ant-card-extra {
                            background: $warning_color;
                            border: 1px solid #FDE9CC;
                        }
                    }
                }
            }
            .ant-list-item {
                border: none;
                padding-left: 10px;
                padding-right: 10px;
                &:nth-of-type(2n+1) {
                    background-color: #F7F7FF;
                }
                &:nth-of-type(2n) {
                    background-color: #fff;
                }
            }
        }
    }
}