$theme_color: #333fff; // 主题色
$gray_color: #646464; // 灰色值
$warning_color: #ffb140; // 警告提示色
$danger_color: #ff4f4d; // 危险色
$header_height: 60px; // 顶部导航栏的高度
$side_nav_width: 260px; // 侧边栏的宽度
.ant-tabs {
    // tabs标签页切换
    .ant-tabs-tab {
        font-weight: 900;
    }
}

@mixin form_item($display, $label_width) {
    // 表单控件中label和表单的排列样式
    .ant-form-item-label {
        display: $display;
        min-width: $label_width;
        text-align: left;
        label {
            color: $gray_color;
        }
    }
    .ant-form-item-control-wrapper {
        display: $display;
    }
}

@mixin ant_card {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        border: none;
    }
}

@mixin left_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        cursor: $cursor;
        .ant-card-head {
            border: none;
            position: relative;
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                width: 6px;
                height: 30px;
                background-color: $theme_color;
                border-radius: 3px;
            }
        }
    }
}

@mixin top_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        text-align: center;
        padding: 25px 0;
        cursor: $cursor;
        border: 1px solid rgba(0, 0, 0, 0);
        transition: border-color 0.3s;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            left: 50%;
            transform: translateX(-50%);
            top: -1px;
            width: 30px;
            height: 6px;
            background: $theme_color;
            border-radius: 3px;
        }
        &:hover {
            border: 1px solid $theme_color;
        }
        .ant-card-head {
            border: none;
            min-height: 0;
            .ant-card-head-title {
                padding: 0;
            }
        }
        .ant-card-body {
            padding: 0;
        }
    }
}
.storage {
    @mixin margin_top($val) {
        margin-top: $val;
    }
    .agent_storage {
        @include margin_top(20px);
        .header {
            overflow: auto;
            .title {
                float: left;
            }
            .toggle_config {
                float: right;
            }
        }
        .select_set {
            margin-top: 20px;
            @include form_item(inline-block, 80px);
            .ant-form-item {
                margin-bottom: 0px;
            }
        }
    }
    .layer_storage {
        @include margin_top(40px);
        color: $gray_color;
    }
    .hide_set_layer_storage {
        @include margin_top(40px);
        color: $gray_color;
        .ant-collapse {
            background: none;
            border: none;
            .ant-collapse-item {
                border-bottom: none;
                .ant-collapse-header {
                    padding: 0;
                    padding-left: 20px;
                    .anticon-right {
                        left: 0;
                        top: 42%;
                    }
                }
            }
            .ant-collapse-content {
                border-top: none;
                .ant-collapse-content-box {
                    padding: 0;
                }
            }
        }
        .ant-tabs-bar {
            border-bottom: none;
        }
        .ant-tabs-tab {
            margin-right: 0 !important;
            border: 1px solid #26a7ff !important;
            font-weight: normal !important;
            background-color: #ebecff !important;
            color: $theme_color !important;
            height: 30px !important;
            line-height: 28px !important;
            &:first-of-type {
                border-radius: 4px 0 0 4px;
                border-right: none !important;
            }
            &:last-of-type {
                border-radius: 0 4px 4px 0;
                border-left: none !important;
            }
        }
        .ant-tabs-tab-active {
            border-bottom: 1px solid #26a7ff !important;
            background-color: $theme_color !important;
            color: #fff !important;
        }
        .ant-tabs-tabpane {
            margin-left: 10px;
            border-left: 1px solid #ececec;
            padding-left: 20px;
        }
    }
}