$theme_color: #333fff; // 主题色
$gray_color: #646464; // 灰色值
$warning_color: #ffb140; // 警告提示色
$danger_color: #ff4f4d; // 危险色
$header_height: 60px; // 顶部导航栏的高度
$side_nav_width: 260px; // 侧边栏的宽度
.ant-tabs {
    // tabs标签页切换
    .ant-tabs-tab {
        font-weight: 900;
    }
}

@mixin form_item($display, $label_width) {
    // 表单控件中label和表单的排列样式
    .ant-form-item-label {
        display: $display;
        min-width: $label_width;
        text-align: left;
        label {
            color: $gray_color;
        }
    }
    .ant-form-item-control-wrapper {
        display: $display;
    }
}

@mixin ant_card {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        border: none;
    }
}

@mixin left_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        cursor: $cursor;
        .ant-card-head {
            border: none;
            position: relative;
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                width: 6px;
                height: 30px;
                background-color: $theme_color;
                border-radius: 3px;
            }
        }
    }
}

@mixin top_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        text-align: center;
        padding: 25px 0;
        cursor: $cursor;
        border: 1px solid rgba(0, 0, 0, 0);
        transition: border-color 0.3s;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            left: 50%;
            transform: translateX(-50%);
            top: -1px;
            width: 30px;
            height: 6px;
            background: $theme_color;
            border-radius: 3px;
        }
        &:hover {
            border: 1px solid $theme_color;
        }
        .ant-card-head {
            border: none;
            min-height: 0;
            .ant-card-head-title {
                padding: 0;
            }
        }
        .ant-card-body {
            padding: 0;
        }
    }
}
.cluster {
    .side_nav_wrap {
        float: left;
        width: $side_nav_width;
        .side_nav {
            padding-top: 30px;
            height: calc(100vh - #{$header_height});
            background-color: #0d1040;
            .cluster_name {
                color: #fff;
                line-height: 40px;
                padding-left: 24px;
            }
            .ant-menu {
                border: none;
                .ant-menu-item {
                    a {
                        color: #fff;
                    }
                }
                .ant-menu-item-selected {
                    background-color: #161c6d;
                }
                .ant-menu-item-active {
                    background-color: #161c6d;
                    position: relative;
                    &::after {
                        content: "";
                        position: absolute;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        transform: scaleY(1);
                        opacity: 1;
                        transition: opacity 0.15s cubic-bezier(0.645, 0.045, 0.355, 1);
                    }
                }
            }
            .side_nav_icon {
                margin-right: 6px;
                font-size: 16px;
                line-height: 40px;
                float: left;
            }
        }
    }
    .cluster_content {
        float: left;
        padding: 30px 100px;
        width: calc(100% - #{$side_nav_width});
    }
}