$theme_color: #333fff; // 主题色
$gray_color: #646464; // 灰色值
$warning_color: #ffb140; // 警告提示色
$danger_color: #ff4f4d; // 危险色
$header_height: 60px; // 顶部导航栏的高度
$side_nav_width: 260px; // 侧边栏的宽度
.ant-tabs {
    // tabs标签页切换
    .ant-tabs-tab {
        font-weight: 900;
    }
}

@mixin form_item($display, $label_width) {
    // 表单控件中label和表单的排列样式
    .ant-form-item-label {
        display: $display;
        min-width: $label_width;
        text-align: left;
        label {
            color: $gray_color;
        }
    }
    .ant-form-item-control-wrapper {
        display: $display;
    }
}

@mixin ant_card {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        border: none;
    }
}

@mixin left_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        cursor: $cursor;
        .ant-card-head {
            border: none;
            position: relative;
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                width: 6px;
                height: 30px;
                background-color: $theme_color;
                border-radius: 3px;
            }
        }
    }
}

@mixin top_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        text-align: center;
        padding: 25px 0;
        cursor: $cursor;
        border: 1px solid rgba(0, 0, 0, 0);
        transition: border-color 0.3s;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            left: 50%;
            transform: translateX(-50%);
            top: -1px;
            width: 30px;
            height: 6px;
            background: $theme_color;
            border-radius: 3px;
        }
        &:hover {
            border: 1px solid $theme_color;
        }
        .ant-card-head {
            border: none;
            min-height: 0;
            .ant-card-head-title {
                padding: 0;
            }
        }
        .ant-card-body {
            padding: 0;
        }
    }
}
.add_stream_model {
    .step {
        color: $gray_color;
        font-size: 12px;
    }
    .select_theme {
        .search_checkbox {
            overflow: auto;
            margin-top: 20px;
            .search_box {
                float: left;
            }
            .check_box {
                float: right;
            }
        }
    }
    .themes {
        margin-top: 30px;
        border-top: 2px solid #C4C4C4;
        border-bottom: 2px solid #C4C4C4;
        .ant-list-item {
            cursor: pointer;
        }
    }
    .second_step {
        .second_step_form {
            @include form_item(block, 100%);
            padding-right: 20px;
            border-right: 1px solid #E0E0E0;
        }
        .query_existing_stream {
            padding-left: 20px;
            .query_existing_stream_title {
                border-bottom: 2px solid #C4C4C4;
                line-height: 34px;
            }
            .existing_stream {
                margin-top: 15px;
                .existing_stream_item {
                    font-size: 12px;
                    color: $gray_color;
                    margin-bottom: 6px;
                }
            }
        }
        .ant-form-item {
            margin-bottom: 0;
        }
    }
}