$theme_color: #333fff; // 主题色
$gray_color: #646464; // 灰色值
$warning_color: #ffb140; // 警告提示色
$danger_color: #ff4f4d; // 危险色
$header_height: 60px; // 顶部导航栏的高度
$side_nav_width: 260px; // 侧边栏的宽度
.ant-tabs {
    // tabs标签页切换
    .ant-tabs-tab {
        font-weight: 900;
    }
}

@mixin form_item($display, $label_width) {
    // 表单控件中label和表单的排列样式
    .ant-form-item-label {
        display: $display;
        min-width: $label_width;
        text-align: left;
        label {
            color: $gray_color;
        }
    }
    .ant-form-item-control-wrapper {
        display: $display;
    }
}

@mixin ant_card {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        border: none;
    }
}

@mixin left_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        cursor: $cursor;
        .ant-card-head {
            border: none;
            position: relative;
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                width: 6px;
                height: 30px;
                background-color: $theme_color;
                border-radius: 3px;
            }
        }
    }
}

@mixin top_ant_card($cursor: auto) {
    .ant-card {
        box-shadow: 3px 3px 12px 0px #dddcdc;
        border-radius: 10px;
        text-align: center;
        padding: 25px 0;
        cursor: $cursor;
        border: 1px solid rgba(0, 0, 0, 0);
        transition: border-color 0.3s;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            left: 50%;
            transform: translateX(-50%);
            top: -1px;
            width: 30px;
            height: 6px;
            background: $theme_color;
            border-radius: 3px;
        }
        &:hover {
            border: 1px solid $theme_color;
        }
        .ant-card-head {
            border: none;
            min-height: 0;
            .ant-card-head-title {
                padding: 0;
            }
        }
        .ant-card-body {
            padding: 0;
        }
    }
}
.head_nav {
    background-color: #0d1040;
    height: $header_height;
    line-height: $header_height;
    border-bottom: none;

    .main_title {
        width: $side_nav_width;

        &:hover {
            background: none !important;
        }
    }

    .ant-menu-submenu {
        top: 0;
        color: #fff;
        border-bottom: none !important;

        &:hover {
            color: #fff;
        }

        .ant-menu-submenu-title {
            &:hover {
                color: #fff;
            }
        }
    }

    .ant-menu-submenu-active {
        background-color: $theme_color;

        span {
            color: #fff;
        }
    }

    .ant-menu-item {
        border-bottom: none !important;
        top: 0;
        &:hover {
            background-color: $theme_color;

            a {
                color: #fff;
            }
        }

        a {
            height: $header_height;
            color: #fff;
        }
    }

    .data_flow_icon {
        margin-right: 4px;
        font-size: 22px;
        line-height: $header_height;
        float: left;
    }
}

.cluster_drop_menu {
    width: 100%;

    .ant-menu-sub {
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 10px;

        .ant-menu-item {
            float: left;
            padding: 0;
            margin-right: 15px;

            a {
                display: inline-block;
                background: #ebecff;
                border: 1px solid #bcc0ff;
                border-radius: 4px;
                line-height: 14px;
                padding: 8px 18px;
                color: $theme_color;
            }

            a.selected {
                background-color: $theme_color;
                color: #fff;
            }
        }

        .ant-menu-item-active {
            a {
                background-color: $theme_color;
                color: #fff;
            }
        }
    }
}